  font-weight: 800;
  color: #fff;
  text-shadow: 3px 3px 7px rgb(92, 92, 92);
}
#my_pages_card_title{
  overflow: hidden; 
  white-space: nowrap;
  font-weight: 800;
  text-overflow: ellipsis;
  text-shadow: 3px 3px 7px #555555;
  text-overflow:ellipsis;
}
#circle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-image: url("../../assets/images/profile_image.png");
  background-size: cover;
  box-shadow: 3px 3px 7px #CCC;
}
</style>

<script>
export default {
  name: 'MyAccount',
  created(){
      this.$store.commit("set", ["pageInfo", "내 매칭글"])
      this.$store.commit("set", ["pageRoute", ""])
  },
  data () {
    return {
        myMajorItems: [
            {
                post_index: 100001,
                title: "캡스톤 디자인 같이 할 사람~",
                head_count_max: 4,
                head_count: 3, 
            },
        ],
        myUnivItems: [
            {
                post_index: 200001,
                title: "아이디어 경진대회 같이 나갈 사람 구해요~",
                head_count_max: 5,
                head_count: 5, 
            },
            {
                post_index: 200002,
                title: "같이 아무 대회 나갈 사람~~",
                head_count_max: 10,
                head_count: 3, 
            },
        ],
        friendsItems: [
            {
                post_index: 300001,
                title: "나랑 친구해 줄 사람!",
                head_count_max: 20,
                head_count: 3, 
            },
        ],
        clubItems: [
            
        ],
    }
  },
  methods: {
    delay_value(index){
      if (index < 4)
        return index * 100
      else return 0
    },
    color (value, max) {
      let $color
      let $temp = value/max
      if ($temp <= 0.5) {
        $color = 'rgb(37,186,14)'
      } else if ($temp > 0.5 && $temp <= 0.75) {
        $color = 'rgb(255, 127, 0)'
      } else if ($temp > 0.75 && $temp < 0.98) {
        $color = 'rgb(245,89,25)'
      }
      else $color = 'rgb(177,30,49)'
      return $color
    },
    gotoLogin(){
        this.$router.push({ path: '/matching/ViewPost' })
    },
  }
}
</script>